.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.sidebar-container {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  z-index: 9997;
  transition: all .5s;
  padding: 15px;
  overflow-y: auto;
}

.sidebar {
  list-style: none;
  margin-block-start: 10em;
  padding-inline-start: 0px;
}

.sidebar li {
  padding: 1em 0;
}

.section {
  display: flex;
  padding-left: 250px;
  align-items: center;
}
.App-header {
  background-color: rgb(46, 45, 43);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
